import * as React from 'react';

import ArticleData from '../data/articleData';
import ModalArticle, { IModalArticleProps } from './modal/modal-article';
import { IModal } from './modal/modal-window';
import ModalWindow from './modal/modal-window';

require('./article.css');

interface IProps {
  id: number;
  data: ArticleData;
}

interface IState {
  modalOpened: boolean;
}

export default class Article extends React.Component<IProps, IState> {

  public constructor(props: IProps) {
    super(props);
    this.state = {
      modalOpened: false
    };

    this.handleClick = this.handleClick.bind(this);
  }

  private renderImage(): React.ReactNode | null {
    const image = this.props.data.Image;
    return image != null ?
      <div className='article-imgpart'>
        <img className='article-image' src={image}/>
      </div> : null;
  }

  private handleClick(): void {
    this.setState({ modalOpened: true });
  }

  private renderText(): React.ReactNode {
    return <div className='article-textpart'>
      <div className='article-textpart-title'>{this.props.data.Title}</div>
      <button onClick={this.handleClick}>Open</button>
      <div className='article-textpart-description'>{this.props.data.Description}</div>
    </div>;
  }

  public render(): React.ReactNode {
    const color = {
      'background-color': this.props.data.BackgroundColor
    } as React.CSSProperties;

    const props: IModalArticleProps = { data: this.props.data };
    const modal: IModal<IModalArticleProps, {}> = new ModalArticle(props);

    return <div id={this.props.id.toString()} className='article' style={color}>
      {this.renderImage()}
      {this.renderText()}
      <ModalWindow opened={this.state.modalOpened} modal={modal} />
    </div>;
  }
}